body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: Roboto, sans-serif;
}

.wrapper {
  max-width: 1600px;
  padding-left: 62px;
  padding-right: 62px;
  margin: 0 auto;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1600px;
  padding-left: 62px;
  padding-right: 62px;
  margin: 0 auto;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 20px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

h1,
.h1 {
  font-size: 36px;
  text-align: center;
}

h2,
.h2 {
  font-size: 24px;
  text-align: center;
}

h3,
.h3 {
  font-size: 22px;
  text-align: center;
}

h4,
.h4 {
  font-size: 16px;
  text-align: center;
}

h5,
.h5 {
  font-size: 15px;
  text-align: center;
}

h6,
.h6 {
  font-size: 14px;
  text-align: center;
}

img {
  max-width: 100%;
  height: 100%;
  width: 100%;
}

.pink {
  border: 1px solid blueviolet !important;
  box-shadow: 0 0 0 5px blueviolet;
}

.main {
  max-width: 100%;
}

.main .header {
  height: 50px;
  background: #d5d2d2;
  display: flex;
  align-items: center;
  position: relative;
}

.main .header .title {
  display: flex;
  align-items: center;
}

.main .header .title .span {
  border: 1px solid blueviolet;
  background: blueviolet;
  width: 30px;
  height: 30px;
  border-radius: 25px;
  margin: 0 10px 0 20px;
}

.main .header .info {
  display: flex;
  position: absolute;
  left: 39%;
  cursor: pointer;
}

.main .header .info .h4_first {
  margin-right: 50px;
}

.main .header .details_menu {
  display: none;
  position: relative;
  margin: 0 5% 0 auto;
}

.main .header .details_menu .parent {
  display: flex;
  position: absolute;
  right: 0;
  top: -5px;
  right: 40px;
}

.main .header .details_menu .parent .h4 {
  border-radius: 20px;
  border: 1px solid #cecece;
  background-color: #f6f6f6;
  padding: 4px 10px 4px 10px;
  white-space: nowrap;
  text-align: end;
  cursor: pointer;
}

.main .header details summary::-webkit-details-marker {
  display: none;
  transition: height 3s ease-in;
  outline: 0;
  outline-offset: 0;
}

.main .header details summary::-webkit-details-marker:active,
.main .header details summary::-webkit-details-marker :hover,
.main .header details summary::-webkit-details-marker :focus {
  outline: 0;
  outline-offset: 0;
}

.main .header details[open] summary ~ * {
  animation: sweep 0.5s ease-in-out;
}

.main .header summary {
  outline: none;
  cursor: pointer;
}

@keyframes sweep {
  0% {
    opacity: 0;
    margin-left: -100%;
  }

  100% {
    opacity: 1;
    margin-left: 0%;
  }
}

.main .search {
  display: flex;
  padding: 40px 0;
}

.main .search select {
  display: block;
  border-radius: 20px;
  border: 1px solid #cecece;
  background-color: #f6f6f6;
  padding: 4px 15px 4px 10px;
  color: #444;
  background-position: 290px -145px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
}

.main .search select:nth-child(2) {
  margin: 0 30px 0 20px;
}

.main .search select:focus {
  outline: none;
}

.main .search .d3 form {
  border: 1px solid #c3b7b7;
}

.main .search .d3 input,
.main .search .d3 button {
  border: none;
  outline: none;
  background: transparent;
}

.main .search .d3 input {
  width: 100%;
  height: 42px;
  padding-left: 40px;
}

.main .search .d3 button {
  height: 42px;
  width: 42px;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}

.main .search .d3 button:before {
  content: "\f002";
  font-family: FontAwesome;
  font-size: 16px;
  color: #f9f0da;
}

.main .search form {
  position: relative;
  width: 500px;
  margin: 0 auto;
}

.main .items .item {
  border: 1px solid #ababab;
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;
  position: relative;
}

.main .items .item .item_name {
  display: flex;
  height: 90px;
  align-items: center;
  width: 100%;
  border-bottom: 0.5px solid #ababab;
}

.main .items .item .item_name img {
  width: 50px;
  height: 50px;
  margin: 10px 20px;
  background: black;
  color: white;
}

.main .items .item .item_name .h3 {
  width: 80%;
  text-align: left;
}

.main .items .item .item_category {
  margin: 10px;
  border-right: 1px solid #ababab;
  padding-right: 70px;
}

.main .items .item .item_category .h5 {
  text-align: left;
}

.main .items .item .item_category .h5 .span {
  color: grey;
}

.main .items .item .item_sub_name {
  display: flex;
  align-items: center;
}

.main .items .item .item_sub_name img {
  width: 30px;
  height: 30px;
  background: blueviolet;
  border-radius: 30px;
}

.main .items .item .item_sub_name .h4 {
  text-align: left;
  margin-left: 15px;
}

.main .items .item .item_button {
  display: flex;
  position: absolute;
  right: 10px;
  bottom: 12px;
  opacity: 0;
}

.main .items .item .item_button .h4 {
  padding: 5px 15px;
  margin: 0 10px;
  color: blueviolet;
  cursor: pointer;
}

.main .items .item .item_button .h4:last-child {
  background: blueviolet;
  border-radius: 5px;
  color: white;
}

.main .items .pink .item_button {
  opacity: 1 !important;
}

@media screen and (max-width: 1024px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }

  .main .header .title {
    margin-right: 20%;
  }

  .main .header .info {
    position: unset;
    align-items: center;
  }

  .main .search .d3 {
    width: 40%;
  }

  .main .search form {
    width: auto;
  }
}

@media screen and (max-width: 850px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .debug > div {
    padding-left: 20px;
    padding-right: 20px;
  }

  .debug > div {
    padding-left: 20px;
    padding-right: 20px;
  }

  .main .header .h4 {
    font-size: 14px;
  }

  .main .search select {
    font-size: 14px;
  }

  .main .search select:nth-child(2) {
    margin: 0 5% 0 3%;
  }

  .main .search .d3 {
    width: 30%;
  }

  .main .search form {
    font-size: 14px;
  }

  .main .items .item .item_name .h3 {
    font-size: 18px;
  }

  .main .items .item .item_category {
    padding-right: 30px;
  }

  .main .items .item .item_category .h5 {
    font-size: 14px;
  }

  .main .items .item .item_sub_name .h4 {
    font-size: 14px;
  }

  .main .items .item .item_button .h4 {
    font-size: 14px;
  }
}

@media screen and (max-width: 740px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .debug > div {
    padding-left: 15px;
    padding-right: 15px;
  }

  .debug > div {
    padding-left: 15px;
    padding-right: 15px;
  }

  .main .header .title {
    margin-right: 15%;
  }

  .main .header .h4 {
    font-size: 12px;
  }

  .main .items .item .item_name .h3 {
    font-size: 16px;
  }

  .main .items .item .item_category .h5 {
    font-size: 13px;
  }

  .main .items .item .item_sub_name .h4 {
    font-size: 13px;
  }

  .main .items .item .item_button .h4:first-child {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }

  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }

  .main .header .title {
    margin-right: 10%;
  }

  .main .header .h4 {
    font-size: 10px;
  }

  .main .header summary .h5 {
    font-size: 10px;
  }

  .main .search {
    flex-wrap: wrap;
    padding: 15px 0;
  }

  .main .search .d3 {
    order: 1;
    width: 73%;
    margin-bottom: 5%;
  }

  .main .items .item .item_name .h3 {
    font-size: 14px;
  }

  .main .items .item .item_category .h5 {
    font-size: 11px;
  }

  .main .items .item .item_sub_name .h4 {
    font-size: 11px;
  }
}

@media screen and (max-width: 500px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }

  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }

  .main .header .title {
    margin-right: unset;
  }

  .main .header .info {
    display: none;
  }

  .main .header .details_menu {
    display: block;
  }

  .main .search select {
    order: 2;
  }

  .main .items .item .item_name .h3 {
    font-size: 12px;
  }

  .main .items .item .item_category {
    padding-right: unset;
    width: 100%;
    border-right: unset;
  }

  .main .items .item .item_sub_name {
    margin: 0 0 10px 10px;
  }
}

@media screen and (max-width: 320px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .debug > div {
    padding-left: 5px;
    padding-right: 5px;
  }

  .debug > div {
    padding-left: 5px;
    padding-right: 5px;
  }
}